<template>
  <PageBody>
    <p class="primary-color">asdasd</p>
    首页
    <button @click="test">text</button>
    <ul>
      <li v-for="(v,i) in colorOption" :key="i" @click="handleSelect(v)"
          style="height: 30px;" :style="'background:'+v"></li>
    </ul>
    <div class="color-box">
      <button class="shade-6-background" style="color:white;width: 140px">
        <span>菜单</span>
        <i class="iconfont gym-icon-show" style="font-size: 24px"></i>
        <i class="primary-color iconfont gym-icon-show" style="font-size: 24px"></i>
      </button>
      <button class="primary-background" style="color:white">type 1</button>
      <button class="primary-color primary-border" style="border:1px solid">type 2</button>
      <button class="primary-color light-8-background">type 3</button>
      <span class="primary-color">高亮文字</span>
    </div>
  </PageBody>
</template>
<script>
import { saveTheme } from 'api/setting'
import { setColor } from 'utils/tool'

export default {
  data () {
    return {
      cssText: '',
      colorOption: [
        '#6494DD', '#25A5EC', '#4054B0', '#F1433A', '#E72366', '#9827A7', '#6D3FB9',
        '#1A988D', '#42B04B', '#FA521F'
      ]
    }
  },
  mounted () {
  },
  methods: {
    handleSelect (color) {
      setColor(color)
      saveTheme({theme: color}).then(res => {
        console.log(res)
      })
    }
  }
}
</script>
<style lang='stylus' scoped>
  .page-container
    width 100%
    height 100%
    display flex
    flex-direction row
    align-items stretch
  .color-box
    margin-top 20px
    display flex
    flex-direction row
    align-items center
    padding 20px
    & > *
      margin-right 20px
    & > button
      width 90px
      height 36px
      border-radius 4px
</style>



